/* CSS Variables */
:root {
  --normal-font: 400;
  --bold-font: 600;
  --bolder-font: 900;
  --primary-color: #004643;
  --secondary-color: #f45d48;
  --line-height: 1.7rem;
  --transition: 0.4s ease-in;
}

body {
  margin: 0;
  font-family: "Raleway Thin", Arial, Helvetica, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: var(--primary-color);
  color:#abd1c6;
}

h1, h2{
  font-weight: bold;
}

h1{
  font-size: 4rem;
  color:#fffffe;
}

h2 {
  font-size: 3rem;
}

p{
  font-size: 1.75rem;
}

.title{
  margin: 0;
  padding: 0 2rem 4rem;
}

.wrapper{
  padding: 5rem 0 0;
}

.row{
  margin: 6rem 0 0;
}

.pc_tablet .content {
  margin:8rem 0 3rem;
  padding:0 6rem 4rem;  
}

.mobile .content {
  margin:8rem 0 3rem;
  padding:0;  
}

.App {
  text-align: center;
}

.project, .gallery, .donation {
  padding-top: 4rem;
}

.about {
  padding-top: 0;
}

/* responsive settings */
.mobile .navbar-header {
  float: left;
}

.mobile .navbar-brand {
  font-size: 20px;
  padding-left: 0;
}

/* header */
.navbar {
  font-size: 1.75rem;
  margin:0;
  padding: 0.25rem 4rem 0.25rem 2rem;
  text-transform: none;
  background-color:#faf9f9;
}

.navbar, .navbar-header, .navbar-nav, .navbar-right a{
  color: #222;
}
.nav-hover:hover{
  color: #222;
}

.navbar-brand{
  margin:0;
  padding-left:4rem;
  font-size: 3rem;
  letter-spacing: 0.1rem;
}


/* carousel */
.btn-carousel{
  margin:2rem 0 6rem;
  padding: 1.75rem 4.5rem;
  background-color: rgba(216, 180, 125, 0.6);
  color:#e8e4e6;
  font-size: 2.5rem;
  font-weight: bold;
}

.btn-carousel a{
  color: #e8e4e6;
  text-decoration: none;
}

.btn-carousel a:hover{
  color: #fffffe;
}

.carousel-inner img{
  width:100%;
}

.carousel-inner h1{
  color: #fff;
  font-family: Arial, Helvetica, sans-serif;
  text-transform: uppercase;
  margin-top:50rem;
  padding-top: 50rem;
}

.carousel-inner p{
  font-size: 2.75rem;
  color: #fff;
  font-weight: bold;
}

.carousel {
  padding-top: 5rem;
}

/* about */
.about h1, p{
  padding: 0 2rem;
}

.mobile .about h1{
  padding-top: 6rem;
}

.about .thumbnail{
  padding:2rem;
  margin: 0;
}

.about .thumbnail p{
  text-align: left;
}

.quote {
  color:#717171;
}

.pc_tablet .about img {
  width: 30%;
}

.mobile .about img {
  width: 25%;
}

/* project */
.project .row{
  margin: 0;
  padding: 0;
}

.project .thumbnail{
  padding:2rem 4rem 4rem;
  margin: 2rem;
}

.project .top{
  margin-top: 0;
  padding-top: 0;
}
.project .thumbnail p{
  text-align: left;
  padding:0 1.5rem 0 0;
  color: #001e1d;
}

.project h2{
  color: #333;
  text-align: left;
  margin-bottom: 2rem;
}

.mobile .project h2{
  font-size: 2rem;
}

.project .caption{
  display: flex;
  padding: 0;
}

.project figure {
  float: left;
}

.project img{
  margin: 0 1.5rem 1.5rem 0;
}

.pc_tablet .project img{
  width: 16rem;
  height: 24rem;
}

.mobile .project img{
  width: 0;
  height: 0;
  display: none;
}

/* donation */
#donation-row{
  text-align: left;
  background-color:#fffffe;
  padding: 2rem 2rem 6rem;
  margin-top: 0;
}

.donation h2{
  font-family:Arial, Helvetica, sans-serif;
  font-size: 5rem;
  color: #001e1d;
}

.donation .col-sm-7 p{
  color: #001e1d;
}

/* donation progress bar */
.progress {
  height: 4rem;
}

.progress-bar {
  font-size: 3rem;
  text-align: center;
  padding: 1rem;
  background-color: var(--secondary-color);
}

.progress-text {
  font-size: 2.5rem;
  color: #001e1d;
}

.progress-text span{
  font-size: 3rem;
  font-weight: bold;
}

.pc_tablet .donate-span-right {
  text-align: right;
}

.pc_tablet .donate-span-middle {
  text-align: center;
}

/* donate-left */
.donate-left {
  margin: 2rem 4rem;
}

.donate-left p{
  font-size: 2rem;
  padding: 1.5rem 0;
}


/* donate-right */
.donate-right {
  margin: 2rem 2rem 0 4rem;
  padding: 2rem 4rem;
  background-color: #e8e4e6;
  border-radius: 0.5rem;
}

.donate-right h3{
  font-size: 3rem;
  color: #222;
  margin-bottom: 4rem;
  text-align: center;
}

.donate-btn{
  font-size: 2rem;
  color: #001e1d;
}

.donate-btn input{
  min-width: 4rem;
  transform:scale(1.0);
  margin:0 2rem 1rem 0;
}

.donate-btn label {
  font-weight: normal;
}

.donate-amount-text{
  font-size: 2rem;
  margin: 4rem 0;
}

.donate-amount-text input{
  width: 12rem;
  height: 3rem;
  border: 1px solid #999;
}

.btn-success{
  width:100%;
  font-size: 2.5rem;
  margin:1.5rem 0 2rem;
  background-color: #abd1c6;
  border: none;
}

.btn-success:hover{
  background-color: #f9bc60;
  border: none;
}

.btn-success::after{
  background-color: #f9bc60;
  border: none;
}

/* footer */
.footer .navbar {
  border: none;
}

.navbar-bottom{
  text-align: justify;
  font-family: Arial, Helvetica, sans-serif;
  color: #818181;
}

.navbar-bottom .row{
  margin: 2rem 0 2rem;
}

.navbar-bottom h3{
  color:#616161;
}

.navbar-bottom span{
  color: #fff;
}

.navbar-bottom a, .navbar-bottom span{
  color:#818181;
}

.copyright-tag{
  text-align: center;
  background-color: #222;
  color: #909090;
  padding: 1.75rem 0;
}